    }
});

onBeforeMount(async () => {
    try {
        await fetch(getAllLessonsUrl)
            .then(response => response.json())
            .then(json => {
                lessons.value = json
                });
    } catch (error) {
        console.log(error);
    }
});

async function postView(id) {
    try {
        await fetch(getLessonUrl + id)
            .then(response => response.json())
            .then(json => {
                foundLesson.value = json
                });
    } catch (error) {
        console.log(error);
    }
}

</script>

